import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import "swiper/css";
import "swiper/css/navigation";

const Graduation = (props)=> {
    const data = props.data
    return(
        <section className="tf-section graduation">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <div className="block-text center">
                            <h1 className="color-text heading-bg" data-aos="fade-in" data-aos-duration="1000"><span></span></h1>
                            <h5 className="color-text sub-title mb-10"  data-aos="fade-up" data-aos-duration="1000"></h5>
                            <h3 className="color-text title mb-28"  data-aos="fade-up" data-aos-duration="1000"> <br /></h3>
                        </div>
                    </div>
                    <div className="row">
                        <Swiper navigation={true} modules={[Navigation]} className="">
                            {data.map(item=>(
                                <SwiperSlide  key={item.key}><img src={item.img} alt="" style={{width: "100%", height: "auto", }}/>
                                    <div className="img-title"><h4>{item.title}</h4></div>
                                </SwiperSlide>
                            ))}
                        </Swiper>
                    </div>
                </div>
            </div>
        </section>
    )
}


export default Graduation;

