import React from "react";
import { Link } from "react-router-dom";
import './Button.css'

const Button = ()=> {

    const downLoad = ()=> {
        var a = document.createElement("a"); //<a></a> 
        a.href =  "/Chiny.pdf"; 
        //ahref . 
        a.download ="Chiny.pdf";
        //.xlsxpdf.fpd
        a.style.display = "none"; // a 
        document.body.appendChild(a);
        // a 
        a.click(); //aahref
        a.remove();
    // a 
    }


    return (
        <Link to="/" className="btn-action"  onClick={downLoad}>pdf</Link>
    )
}

export default Button;